<template>
  <div class="container">
    <div class="row align-items-center justify-content-center">
      <div class="col-4">
        <form id="registerForm" @submit="register">
          <div class="form-group">
            <label for="username">用户名</label>
            <input
              type="text"
              class="form-control"
              id="username"
              placeholder="请输入用户名"
              required
              v-model="model.username"
            />
          </div>
          <div class="form-group">
            <label for="password">密码</label>
            <input
              type="password"
              class="form-control"
              id="password"
              placeholder="请输入密码"
              required
              v-model="model.password"
            />
          </div>
          <div class="form-group">
            <label for="again_password">确认密码</label>
            <input
              type="password"
              class="form-control"
              id="again_password"
              placeholder="请重新输入密码"
              required
              v-model="model.again_password"
            />
          </div>
          <button type="submit" class="btn btn-primary">注册</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, inject } from "vue";
import { useRouter } from "vue-router";
export default {
  name: "Register",
  setup() {
    const model = reactive({});
    const $http = inject("$http");
    const router = useRouter();
    console.log($http);
    const register = async () => {
      if (model.password !== model.again_password) {
        alert("密码前后不一致");
        model.password = "";
        model.again_password = "";
      } else {
        const res = await $http.post("/register", model);
        if (res.data.status === 0) {
          alert(res.data.message);
          router.push("/login");
        } else {
          alert(res.data.message);
        }
      }

      return false;
    };
    return {
      model,
      register,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body,
.container,
.row {
  height: 100%;
}
</style>
