<template>
  <div class="container">
    <div class="row align-items-center justify-content-center">
      <div class="col-4">
        <form id="loginForm" @submit="login">
          <div class="form-group">
            <label for="username">用户名</label>
            <input
              type="text"
              class="form-control"
              id="username"
              placeholder="请输入用户名"
              required
              v-model="model.username"
            />
          </div>
          <div class="form-group">
            <label for="password">密码</label>
            <input
              type="password"
              class="form-control"
              id="password"
              placeholder="请输入密码"
              required
              v-model="model.password"
            />
          </div>
          <button type="submit" class="btn btn-primary" id="login">登录</button>
          <span class="go_register"
            ><a @click="() => $router.push('/register')">注册账号</a></span
          >
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, inject } from "vue";
import { useRouter } from "vue-router";
export default {
  name: "Login",
  setup() {
    const model = reactive({});
    const $http = inject("$http");
    const router = useRouter();
    const login = async () => {
      const res = await $http.post("/login", model);
      if (res.data.status === 0) {
        localStorage.setItem("username", model.username);
        router.push("/success");
      } else {
        alert(res.data.message);
      }
      return false;
    };
    return {
      model,
      login,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body,
.container,
.row {
  height: 100%;
}

.go_register {
  float: right;
  margin-top: 10px;
}
</style>
